<template>
  <div>
    <h2>Stocks Chart</h2>

    <div style="margin: 12px 0; display:flex; gap:8px; align-items:center; flex-wrap: wrap;">
      <label>
        Symbole (Komma):
        <input v-model.trim="symbolsInput" @keyup.enter="reload()" placeholder="AAPL,MSFT,GOOGL" />
      </label>
      <button @click="reload()" :disabled="loading">Laden</button>
      <span v-if="loading">lädt…</span>
      <span v-if="errorMsg" style="color:#b00;">{{ errorMsg }}</span>
    </div>

    <div style="position: relative; width: 100%; height: 360px;">
      <canvas ref="chartCanvas"></canvas>
    </div>

    <h3 style="margin-top:12px;">Letzte Preise (erstes Symbol):</h3>
    <ul>
      <li v-for="(price, index) in lastPricesFirst" :key="index">
        {{ index + 1 }}. {{ price.toFixed(2) }} USD
      </li>
    </ul>
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from "vue";

export default {
  name: "StockChart",
  setup() {
    const symbolsInput = ref("AAPL,MSFT");
    const chartCanvas = ref(null);
    const errorMsg = ref("");
    const loading = ref(false);
    const lastPricesFirst = ref([]);

    let chart;            // Chart.js-Instanz
    let abortController;  // für laufende Fetch-Requests

    // Chart.js nur bei Bedarf laden und nur nötige Komponenten registrieren
    const loadChartJs = async () => {
      const ChartJS = await import("chart.js");
      const {
        Chart,
        LineController, LineElement, PointElement,
        LinearScale, CategoryScale,
        Tooltip, Legend,
      } = ChartJS;
      Chart.register(
          LineController, LineElement, PointElement,
          LinearScale, CategoryScale, Tooltip, Legend
      );
      return Chart;
    };

    // Einzelne Serie laden → Array [{date, close}, ...] (chronologisch aufsteigend)
    const fetchSeries = async (sym) => {
      // HINWEIS: Für Produktion einen Backend-Proxy (z. B. /api/stock/:symbol) nutzen,
      // damit der API-Key nicht im Frontend liegt.
      const url = `/api/stocks/${encodeURIComponent(sym)}`;
      const res = await fetch(url, { signal: abortController.signal });
      if (!res.ok) throw new Error(`${sym}: HTTP ${res.status}`);
      const data = await res.json();
      const series = (data.historical || []).slice(0, 180).reverse(); // bis 180 Tage, alt→neu
      return series.map(p => ({ date: p.date, close: p.close }));
    };

    // Mehrere Symbole laden und Daten nach Datum ausrichten
    const loadAligned = async (symbols) => {
      const results = await Promise.all(symbols.map(async (s) => {
        try {
          const series = await fetchSeries(s);
          return { symbol: s, series };
        } catch (e) {
          errorMsg.value = (errorMsg.value ? errorMsg.value + " | " : "") + (e.message || String(e));
          return { symbol: s, series: [] };
        }
      }));

      // Alle Datumswerte sammeln & sortieren (YYYY-MM-DD lässt sich lexikographisch sortieren)
      const allDatesSet = new Set();
      results.forEach(({ series }) => series.forEach(p => allDatesSet.add(p.date)));
      const labels = Array.from(allDatesSet).sort();

      // Symbol → Map(date→close)
      const perSymbolMaps = Object.fromEntries(results.map(({ symbol, series }) => [
        symbol, Object.fromEntries(series.map(p => [p.date, p.close]))
      ]));

      // Dataset pro Symbol, Werte passend zur labels-Reihenfolge (Lücken = null => Liniensprünge)
      const datasets = results.map(({ symbol }) => ({
        label: symbol,
        data: labels.map(d => perSymbolMaps[symbol][d] ?? null),
        tension: 0.2,
      }));

      // Letzte 5 Preise für erstes Symbol
      if (results[0] && results[0].series.length) {
        lastPricesFirst.value = results[0].series.slice(-5).map(p => p.close).reverse();
      } else {
        lastPricesFirst.value = [];
      }

      return { labels, datasets };
    };

    const formatDate = (yyyyMmDd) => {
      const d = new Date(yyyyMmDd + "T00:00:00Z");
      return new Intl.DateTimeFormat(undefined, { year: "2-digit", month: "2-digit", day: "2-digit" }).format(d);
    };

    const reload = async () => {
      if (abortController) abortController.abort();
      abortController = new AbortController();
      loading.value = true;
      errorMsg.value = "";
      try {
        const symbols = symbolsInput.value
            .split(",")
            .map(s => s.trim().toUpperCase())
            .filter(Boolean);
        if (!symbols.length) throw new Error("Bitte mindestens ein Symbol angeben.");
        const { labels, datasets } = await loadAligned(symbols);
        await renderChart(labels, datasets);
      } catch (e) {
        errorMsg.value = e.message || String(e);
        await renderChart([], []);
      } finally {
        loading.value = false;
      }
    };

    const renderChart = async (labels, datasets) => {
      const Chart = await loadChartJs();
      const ctx = chartCanvas.value.getContext("2d");
      if (chart) chart.destroy();
      chart = new Chart(ctx, {
        type: "line",
        data: { labels, datasets },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          interaction: { mode: "nearest", intersect: false },
          plugins: {
            legend: { display: true },
            tooltip: {
              enabled: true,
              callbacks: {
                title: (items) => items.length ? formatDate(items[0].label) : "",
                label: (ctx) => `${ctx.dataset.label}: ${ctx.formattedValue} USD`,
              },
            },
          },
          scales: {
            x: {
              display: true,
              ticks: {
                callback: (_, idx) => formatDate(labels[idx]),
                maxRotation: 0,
                autoSkip: true,
              },
            },
            y: { display: true },
          },
        },
      });
    };

    onMounted(async () => { await reload(); });
    onUnmounted(() => {
      if (abortController) abortController.abort();
      if (chart) chart.destroy();
    });

    return { symbolsInput, chartCanvas, errorMsg, loading, lastPricesFirst, reload };
  },
};
</script>

<style scoped>
canvas { width: 100%; height: 100%; }
</style>